@import '../../styles/vars.scss';


.resumeCard {
    width: 100%;
    min-height: 280px;
    border: 1px solid $border-color;
    box-shadow: 0 0 10px rgba(59, 56, 56, 0.856);
    border-radius: 7px;
    color: $txt-color;
    text-align: justify;
    background: $side-color;
    padding: 50px 30px 30px 30px;

    .title {
        color: $uppercase;
        font-size: 20px;
        margin-bottom: 7px;
    }

    .desc {
        font-size: 15px;
    }

    .divider {
        height: 1px;
        align-self: center;
        background: $border-color;
        margin: 15px 0px;
    }
}

.icon {
    margin: 0px 5px;
    color: $uppercase;
    width: 25px;
    height: 25px;
}

.cardIcon {
    margin: 0px 5px;
    color: $border-color;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 5px;
    right: 30px;
}
